import React, { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import toast from 'react-hot-toast';
import { usersAPI } from '../services/api';
import {
  Button,
  IconButton,
  Form,
  FormGroup,
  Label,
  TextArea,
  Avatar,
  LoadingSpinner,
  CloseIcon,
  EditIcon,
  TrashIcon,
  SendIcon,
  LockIcon,
  EyeIcon
} from './UI';

const CommentsContainer = styled.div`
  background: white;
  border-radius: 15px;
  padding: 20px;
  margin: 15px 0;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
`;

const CommentsHeader = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  
  .title {
    font-size: 18px;
    font-weight: 600;
    color: #2d3748;
  }
  
  .count {
    background: #dc3522;
    color: white;
    padding: 4px 12px;
    border-radius: 20px;
    font-size: 12px;
    font-weight: 600;
  }
`;

const CommentForm = styled(Form)`
  margin-bottom: 20px;
  
  .form-row {
    display: flex;
    gap: 10px;
    align-items: flex-end;
  }
  
  .comment-input {
    flex: 1;
  }
  
  .form-options {
    display: flex;
    gap: 15px;
    align-items: center;
    margin-top: 10px;
  }
  
  .privacy-toggle {
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    
    input[type="checkbox"] {
      margin: 0;
    }
    
    .toggle-label {
      font-size: 14px;
      color: #4a5568;
    }
  }
`;

const CommentsList = styled.div`
  max-height: 400px;
  overflow-y: auto;
`;

const CommentItem = styled(motion.div)`
  display: flex;
  gap: 12px;
  padding: 15px;
  border-radius: 12px;
  background: #f7fafc;
  margin-bottom: 10px;
  transition: all 0.2s ease;
  border-left: 4px solid ${props => props.$isPublic ? '#38a169' : '#805ad5'};
  
  &:hover {
    background: #edf2f7;
  }
  
  .comment-avatar {
    flex-shrink: 0;
  }
  
  .comment-content {
    flex: 1;
    min-width: 0;
  }
  
  .comment-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 5px;
  }
  
  .comment-author {
    font-weight: 600;
    color: #2d3748;
    font-size: 14px;
  }
  
  .comment-time {
    font-size: 12px;
    color: #718096;
  }
  
  .comment-text {
    color: #4a5568;
    font-size: 14px;
    line-height: 1.4;
    margin-bottom: 8px;
  }
  
  .comment-actions {
    display: flex;
    gap: 8px;
    align-items: center;
  }
  
  .edit-indicator {
    font-size: 11px;
    color: #718096;
    font-style: italic;
  }
  
  .privacy-badge {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 11px;
    padding: 2px 6px;
    border-radius: 10px;
    background: ${props => props.$isPublic ? '#c6f6d5' : '#e9d8fd'};
    color: ${props => props.$isPublic ? '#22543d' : '#553c9a'};
  }
`;

const EditForm = styled.div`
  margin-top: 10px;
  
  .edit-row {
    display: flex;
    gap: 8px;
    align-items: flex-end;
  }
  
  .edit-input {
    flex: 1;
  }
`;

const EmptyState = styled.div`
  text-align: center;
  padding: 40px 20px;
  color: #718096;
  
  .icon {
    font-size: 48px;
    margin-bottom: 15px;
    opacity: 0.5;
  }
  
  h3 {
    margin: 0 0 8px 0;
    font-size: 16px;
    color: #2d3748;
  }
  
  p {
    margin: 0;
    font-size: 14px;
  }
`;

const LoadMoreButton = styled(Button)`
  width: 100%;
  margin-top: 15px;
`;

const ProfileComments = ({ username, currentUser, isOwnProfile = false }) => {
  // ,  username 
  if (!username) {
    return (
      <CommentsContainer>
        <div style={{ textAlign: 'center', padding: '20px', color: '#718096' }}>
          :     
        </div>
      </CommentsContainer>
    );
  }
  const [commentText, setCommentText] = useState('');
  const [isPublic, setIsPublic] = useState(true);
  const [editingComment, setEditingComment] = useState(null);
  const [editText, setEditText] = useState('');
  const [page, setPage] = useState(1);
  const [includePrivate, setIncludePrivate] = useState(false);
  const queryClient = useQueryClient();

  //  
  const { data: commentsData, isLoading, error } = useQuery(
    ['profile-comments', username, page, includePrivate],
    () => usersAPI.getProfileComments(username, page, 10, includePrivate),
    {
      keepPreviousData: true,
      enabled: !!username, //      username
      onError: (error) => {
        toast.error('   ');
      }
    }
  );

  // 
  const createCommentMutation = useMutation(
    async (data) => {
      console.log('Mutation function called with data:', data);
      console.log('Data types:', {
        username: typeof data.username,
        commentText: typeof data.commentText,
        isPublic: typeof data.isPublic
      });
      
      if (typeof data.username !== 'string') {
        throw new Error(`Username must be string, got ${typeof data.username}: ${data.username}`);
      }
      
      return await usersAPI.createProfileComment(data.username, data.commentText, data.isPublic);
    },
    {
    onSuccess: () => {
      toast.success(' ');
      setCommentText('');
      queryClient.invalidateQueries(['profile-comments', username]);
      setPage(1); //    
    },
    onError: (error) => {
      toast.error(error.response?.data?.message || '   ');
    }
  });

  const updateCommentMutation = useMutation(
    (data) => usersAPI.updateProfileComment(data.commentId, data.commentText),
    {
    onSuccess: () => {
      toast.success(' ');
      setEditingComment(null);
      setEditText('');
      queryClient.invalidateQueries(['profile-comments', username]);
    },
    onError: (error) => {
      toast.error(error.response?.data?.message || '   ');
    }
  });

  const deleteCommentMutation = useMutation(
    (data) => usersAPI.deleteProfileComment(data.commentId),
    {
    onSuccess: () => {
      toast.success(' ');
      queryClient.invalidateQueries(['profile-comments', username]);
    },
    onError: (error) => {
      toast.error(error.response?.data?.message || '   ');
    }
  });

  // 
  const handleSubmitComment = (e) => {
    e.preventDefault();
    
    //   
    console.log('Submitting profile comment:', {
      commentText,
      trimmed: commentText.trim(),
      length: commentText.length,
      trimmedLength: commentText.trim().length,
      username,
      isPublic
    });
    
    if (!commentText || commentText.trim().length === 0) {
      toast.error('  ');
      return;
    }
    
    const commentData = {
      username, 
      commentText: commentText.trim(),
      isPublic 
    };
    
    console.log('Calling createProfileCommentMutation with:', commentData);
    console.log('CommentData types:', {
      username: typeof commentData.username,
      commentText: typeof commentData.commentText,
      isPublic: typeof commentData.isPublic
    });
    console.log('Username value:', commentData.username);
    
    //  
    if (typeof commentData.username !== 'string') {
      console.error('Username is not a string in commentData:', commentData.username);
      toast.error(':    ');
      return;
    }
    
    createCommentMutation.mutate(commentData);
  };

  const handleEditComment = (comment) => {
    setEditingComment(comment.id);
    setEditText(comment.text);
  };

  const handleUpdateComment = () => {
    if (!editText.trim()) {
      toast.error('  ');
      return;
    }
    updateCommentMutation.mutate({ 
      commentId: editingComment, 
      commentText: editText.trim() 
    });
  };

  const handleDeleteComment = (commentId) => {
    if (window.confirm(' ,     ?')) {
      deleteCommentMutation.mutate(commentId);
    }
  };

  const handleLoadMore = () => {
    setPage(prev => prev + 1);
  };

  const canEditComment = (comment) => {
    return currentUser && comment.author?.login === currentUser;
  };

  const canDeleteComment = (comment) => {
    return currentUser && (comment.author?.login === currentUser || username === currentUser);
  };

  if (isLoading && page === 1) {
    return (
      <CommentsContainer>
        <LoadingSpinner />
      </CommentsContainer>
    );
  }

  const comments = commentsData?.comments || [];
  const totalComments = commentsData?.pagination?.total || commentsData?.total || 0;
  const hasMore = comments.length < totalComments;

  return (
    <CommentsContainer>
      <CommentsHeader>
        <div className="title">  </div>
        <div className="count">{totalComments}</div>
      </CommentsHeader>

      {/*    */}
      {currentUser && !isOwnProfile && (
        <CommentForm onSubmit={handleSubmitComment}>
          <FormGroup className="comment-input">
            <Label> </Label>
            <TextArea
              placeholder="    ..."
              value={commentText}
              onChange={(e) => setCommentText(e.target.value)}
              $minHeight="80px"
              maxLength={1000}
            />
          </FormGroup>
          
          <div className="form-options">
            <div className="privacy-toggle">
              <input
                type="checkbox"
                id="comment-privacy"
                checked={isPublic}
                onChange={(e) => setIsPublic(e.target.checked)}
              />
              <label htmlFor="comment-privacy" className="toggle-label">
                {isPublic ? ' ' : ' '}
              </label>
            </div>
            
            <div style={{ fontSize: '12px', color: '#718096' }}>
              {commentText.length}/1000 
            </div>
          </div>
          
          <div className="form-row">
            <Button 
              type="submit" 
              disabled={createCommentMutation.isLoading || !commentText.trim()}
              $size="small"
            >
              <SendIcon />
              {createCommentMutation.isLoading ? '...' : ''}
            </Button>
          </div>
        </CommentForm>
      )}

      {/*    (   ) */}
      {isOwnProfile && (
        <div style={{ marginBottom: '15px' }}>
          <label style={{ display: 'flex', alignItems: 'center', gap: '8px', cursor: 'pointer' }}>
            <input
              type="checkbox"
              checked={includePrivate}
              onChange={(e) => setIncludePrivate(e.target.checked)}
            />
            <span style={{ fontSize: '14px', color: '#4a5568' }}>
                
            </span>
          </label>
        </div>
      )}

      {/*   */}
      <CommentsList>
        {comments.length > 0 ? (
          <AnimatePresence>
            {comments.map((comment) => (
              <CommentItem
                key={comment.id}
                $isPublic={comment.is_public}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -20 }}
                transition={{ duration: 0.2 }}
              >
                <div className="comment-avatar">
                  <Avatar 
                    src={comment.author?.avatar ? `/uploads/${comment.author.avatar}` : null}
                    alt={comment.author?.name || comment.author?.login}
                    size="40px"
                  />
                </div>
                
                <div className="comment-content">
                  <div className="comment-header">
                    <span className="comment-author">
                      {comment.author?.name || comment.author?.login}
                    </span>
                    <span className="comment-time">
                      {usersAPI.utils?.formatTimeAgo?.(comment.created_at) || 
                       new Date(comment.created_at).toLocaleDateString()}
                    </span>
                    {comment.is_edited && (
                      <span className="edit-indicator">()</span>
                    )}
                    <div className="privacy-badge">
                      {comment.is_public ? (
                        <>
                          <EyeIcon />
                          
                        </>
                      ) : (
                        <>
                          <LockIcon />
                          
                        </>
                      )}
                    </div>
                  </div>
                  
                  {editingComment === comment.id ? (
                    <EditForm>
                      <TextArea
                        value={editText}
                        onChange={(e) => setEditText(e.target.value)}
                        $minHeight="60px"
                        maxLength={1000}
                      />
                      <div className="edit-row">
                        <div style={{ fontSize: '12px', color: '#718096' }}>
                          {editText.length}/1000 
                        </div>
                        <Button 
                          onClick={handleUpdateComment}
                          disabled={updateCommentMutation.isLoading || !editText.trim()}
                          $size="small"
                        >
                          
                        </Button>
                        <Button 
                          onClick={() => setEditingComment(null)}
                          $variant="secondary"
                          $size="small"
                        >
                          
                        </Button>
                      </div>
                    </EditForm>
                  ) : (
                    <div className="comment-text">{comment.text}</div>
                  )}
                  
                  <div className="comment-actions">
                    {canEditComment(comment) && editingComment !== comment.id && (
                      <IconButton
                        onClick={() => handleEditComment(comment)}
                        $size="small"
                        $variant="secondary"
                      >
                        <EditIcon />
                      </IconButton>
                    )}
                    
                    {canDeleteComment(comment) && (
                      <IconButton
                        onClick={() => handleDeleteComment(comment.id)}
                        $size="small"
                        $variant="danger"
                      >
                        <TrashIcon />
                      </IconButton>
                    )}
                  </div>
                </div>
              </CommentItem>
            ))}
          </AnimatePresence>
        ) : (
          <EmptyState>
            <div className="icon"></div>
            <h3> </h3>
            <p> ,      </p>
          </EmptyState>
        )}
      </CommentsList>

      {/*  " " */}
      {hasMore && (
        <LoadMoreButton
          onClick={handleLoadMore}
          disabled={isLoading}
          $variant="secondary"
        >
          {isLoading ? '...' : ' '}
        </LoadMoreButton>
      )}
    </CommentsContainer>
  );
};

export default ProfileComments;
