import React, { useState } from 'react';
import styled from 'styled-components';
import { FormGroup, FormRow } from '../UI';
import CountrySelector from './CountrySelector';
import CitySelector from './CitySelector';

const LocationContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
`;

const LocationRow = styled.div`
  display: grid;
  grid-template-columns: ${props => props.$layout === 'stacked' ? '1fr' : '1fr 1fr'};
  gap: 15px;
  
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    gap: 12px;
  }
`;

const ValidationInfo = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
  color: ${props => props.$valid ? '#38a169' : '#718096'};
  margin-top: 5px;
  
  &::before {
    content: '${props => props.$valid ? '' : ''}';
    font-size: 12px;
    color: ${props => props.$valid ? '#38a169' : '#a0aec0'};
  }
`;

const LocationSelector = ({
  // 
  countryValue = '',
  cityValue = '',
  
  // 
  onCountryChange,
  onCityChange,
  onCountrySelect,
  onCitySelect,
  onLocationChange, // Callback    
  
  //  
  countryError,
  cityError,
  
  //  
  layout = 'side-by-side', // 'side-by-side' | 'stacked'
  countryLabel = '',
  cityLabel = '',
  countryPlaceholder = ' ...',
  cityPlaceholder = ' ...',
  
  // 
  required = false,
  disabled = false,
  showValidation = false,
  
  // 
  className
}) => {
  const [isValidLocation, setIsValidLocation] = useState(false);

  const handleCountryChange = (country) => {
    //     
    if (onCityChange && cityValue) {
      onCityChange('');
    }
    
    if (onCountryChange) {
      onCountryChange(country);
    }
    
    //   callback  
    if (onLocationChange) {
      onLocationChange({
        country,
        city: '', //  
        isComplete: false
      });
    }
    
    setIsValidLocation(false);
  };

  const handleCityChange = (city) => {
    if (onCityChange) {
      onCityChange(city);
    }
    
    //   
    const isComplete = !!(countryValue && city);
    setIsValidLocation(isComplete);
    
    //   callback  
    if (onLocationChange) {
      onLocationChange({
        country: countryValue,
        city,
        isComplete
      });
    }
  };

  const handleCountrySelect = (countryData) => {
    if (onCountrySelect) {
      onCountrySelect(countryData);
    }
  };

  const handleCitySelect = (cityData) => {
    //   
    const isComplete = !!(countryValue && cityData.city);
    setIsValidLocation(isComplete);
    
    if (onCitySelect) {
      onCitySelect(cityData);
    }
    
    //   callback   
    if (onLocationChange) {
      onLocationChange({
        country: countryValue,
        city: cityData.city,
        region: cityData.region,
        isComplete,
        fullData: {
          country: countryValue,
          city: cityData.city,
          region: cityData.region
        }
      });
    }
  };

  const renderValidationInfo = () => {
    if (!showValidation) return null;
    
    if (isValidLocation) {
      return (
        <ValidationInfo $valid={true}>
           
        </ValidationInfo>
      );
    }
    
    if (countryValue && !cityValue) {
      return (
        <ValidationInfo $valid={false}>
             
        </ValidationInfo>
      );
    }
    
    if (!countryValue) {
      return (
        <ValidationInfo $valid={false}>
             
        </ValidationInfo>
      );
    }
    
    return null;
  };

  return (
    <LocationContainer className={className}>
      <LocationRow $layout={layout}>
        <CountrySelector
          value={countryValue}
          onChange={handleCountryChange}
          onSelect={handleCountrySelect}
          error={countryError}
          label={countryLabel}
          placeholder={countryPlaceholder}
          required={required}
          disabled={disabled}
        />
        
        <CitySelector
          country={countryValue}
          value={cityValue}
          onChange={handleCityChange}
          onSelect={handleCitySelect}
          error={cityError}
          label={cityLabel}
          placeholder={cityPlaceholder}
          required={required}
          disabled={disabled}
        />
      </LocationRow>
      
      {renderValidationInfo()}
    </LocationContainer>
  );
};

//     LocationSelector  
export const useLocationSelector = (initialCountry = '', initialCity = '') => {
  const [country, setCountry] = useState(initialCountry);
  const [city, setCity] = useState(initialCity);
  const [isComplete, setIsComplete] = useState(!!(initialCountry && initialCity));

  const handleLocationChange = ({ country: newCountry, city: newCity, isComplete: complete }) => {
    setCountry(newCountry);
    setCity(newCity);
    setIsComplete(complete);
  };

  const reset = () => {
    setCountry('');
    setCity('');
    setIsComplete(false);
  };

  const setLocation = (newCountry, newCity) => {
    setCountry(newCountry);
    setCity(newCity);
    setIsComplete(!!(newCountry && newCity));
  };

  return {
    country,
    city,
    isComplete,
    handleLocationChange,
    reset,
    setLocation,
    //     react-hook-form
    setValue: (field, value) => {
      if (field === 'country') {
        setCountry(value);
        if (value !== country) {
          setCity(''); //     
          setIsComplete(false);
        }
      } else if (field === 'city') {
        setCity(value);
        setIsComplete(!!(country && value));
      }
    }
  };
};

export default LocationSelector;