const Register = () => {
  const navigate = useNavigate();
  const [currentStep, setCurrentStep] = useState(1);
  const [selectedStatus, setSelectedStatus] = useState('');
  const [emailCode, setEmailCode] = useState('');
  const [isCodeSent, setIsCodeSent] = useState(false);
  const [codeTimer, setCodeTimer] = useState(0);
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [formProgress, setFormProgress] = useState(0);
  
  const {
    register,
    handleSubmit,
    watch,
    getValues,
    setValue,
    clearErrors,
    formState: { errors }
  } = useForm({
    defaultValues: {
      country: '',
      city: ''
    }
  });

  const password = watch('password');
  const email = watch('email');
  const allFields = watch();

  //    
  useEffect(() => {
    let requiredFields = ['login', 'email', 'password', 'confirmPassword', 'searchStatus', 'searchAge', 'city'];
    
    //      
    if (selectedStatus === ' (+)' || selectedStatus === ' (+)') {
      requiredFields.push('manBirthday', 'womanBirthday');
    } else if (selectedStatus === '') {
      requiredFields.push('manBirthday');
    } else if (selectedStatus === '') {
      requiredFields.push('womanBirthday');
    }
    
    // ,        
    if (allFields.location && Array.isArray(allFields.location) && allFields.location.length > 0) {
      requiredFields.push('location');
    }
    
    const filledFields = requiredFields.filter(field => {
      if (field === 'location') {
        return allFields.location && Array.isArray(allFields.location) && allFields.location.length > 0;
      }
      return allFields[field];
    });
    
    const progress = (filledFields.length / requiredFields.length) * 100;
    setFormProgress(progress);
  }, [allFields, selectedStatus]);

  //     
  useEffect(() => {
    let interval;
    if (codeTimer > 0) {
      interval = setInterval(() => {
        setCodeTimer(prev => prev - 1);
      }, 1000);
    }
    return () => clearInterval(interval);
  }, [codeTimer]);

  //    
  const sendCodeMutation = useMutation(authAPI.sendCode, {
    onSuccess: () => {
      toast.success('    email');
      setIsCodeSent(true);
      setCodeTimer(120); // 2 
    },
    onError: (error) => {
      toast.error(apiUtils.handleError(error));
    }
  });

  //   
  const registerMutation = useMutation(authAPI.register, {
    onSuccess: (data) => {
      toast.success(' !    SwingFox!');
      navigate('/');
    },
    onError: (error) => {
      toast.error(apiUtils.handleError(error));
    }
  });

  const handleSendCode = () => {
    if (!email) {
      toast.error(' email ');
      return;
    }
    sendCodeMutation.mutate(email);
  };

  const onSubmit = (data) => {
    if (!emailCode) {
      toast.error('    email');
      return;
    }

    if (!selectedStatus) {
      toast.error('  ');
      return;
    }

    //      
    let individualData = {};
    
    if (selectedStatus === ' (+)' || selectedStatus === ' (+)') {
      //   -    
      individualData = {
        date: `${data.manBirthday}_${data.womanBirthday}`,
        height: `${data.manHeight || ''}_${data.womanHeight || ''}`,
        weight: `${data.manWeight || ''}_${data.womanWeight || ''}`,
        smoking: `${data.manSmoking || 'no_matter'}_${data.womanSmoking || 'no_matter'}`,
        alko: `${data.manAlko || 'no_matter'}_${data.womanAlko || 'no_matter'}`
      };
    } else {
      //   -     
      if (selectedStatus === '') {
        individualData = {
          date: data.manBirthday,
          height: data.manHeight || '',
          weight: data.manWeight || '',
          smoking: data.manSmoking || 'no_matter',
          alko: data.manAlko || 'no_matter'
        };
      } else if (selectedStatus === '') {
        individualData = {
          date: data.womanBirthday,
          height: data.womanHeight || '',
          weight: data.womanWeight || '',
          smoking: data.womanSmoking || 'no_matter',
          alko: data.womanAlko || 'no_matter'
        };
      }
    }

    //   location ( )
    const locationArray = Array.isArray(data.location) ? data.location : [];
    const locationString = locationArray.join('&&');

    const submitData = {
      email: data.email,
      mail_code: emailCode,
      login: data.login,
      password: data.password,
      about: {
        status: selectedStatus,
        country: data.country || '',
        city: data.city,
        search_status: data.searchStatus,
        search_age: data.searchAge,
        location: locationString,
        mobile: data.mobile || '',
        info: data.info || ''
      },
      individual: individualData
    };

    registerMutation.mutate(submitData);
  };

  const statusTypes = [
    { value: ' (+)', label: ' (+)' },
    { value: ' (+)', label: ' (+)' },
    { value: '', label: '' },
    { value: '', label: '' }
  ];

  const searchStatusTypes = [
    { value: ' (+)', label: ' (+)' },
    { value: ' (+)', label: ' (+)' },
    { value: '', label: '' },
    { value: '', label: '' }
  ];

  const ageRanges = [
    { value: '   ', label: '   ' },
    { value: ' ', label: ' ' },
    { value: '     +/- 5 ', label: '     +/- 5 ' },
    { value: '     +/- 10 ', label: '     +/- 10 ' }
  ];

  const smokingOptions = [
    { value: '      ', label: '      ' },
    { value: ' ,      ', label: ' ,      ' },
    { value: ',    -   ', label: ',    -   ' },
    { value: '        ', label: '        ' }
  ];

  const alkoOptions = [
    { value: '  ', label: '  ' },
    { value: '  ,       ', label: '  ,       ' },
    { value: ',   ,   ', label: ',   ,   ' },
    { value: ' ,     ', label: ' ,     ' }
  ];

  //       
  useEffect(() => {
    if (allFields.searchStatus && allFields.searchAge && selectedStatus) {
      // ,        
      let canProceed = true;
      
      if (selectedStatus === ' (+)' || selectedStatus === ' (+)') {
        canProceed = allFields.manBirthday && allFields.womanBirthday;
      } else if (selectedStatus === '') {
        canProceed = allFields.manBirthday;
      } else if (selectedStatus === '') {
        canProceed = allFields.womanBirthday;
      }
      
      if (canProceed) {
        setCurrentStep(3);
      } else {
        setCurrentStep(2);
      }
    } else if (allFields.login && allFields.email && allFields.password) {
      setCurrentStep(2);
    } else {
      setCurrentStep(1);
    }
  }, [allFields, selectedStatus]);

  return (
    <RegisterContainer>
      <RegisterCard>
        <LogoSection>
          <Logo>SF</Logo>
          <Title>  SwingFox</Title>
          <Subtitle>    </Subtitle>
        </LogoSection>
        
        <StepIndicator>
          <Step $active={currentStep >= 1}>1</Step>
          <Step $active={currentStep >= 2}>2</Step>
          <Step $active={currentStep >= 3}>3</Step>
        </StepIndicator>

        <ProgressBar $progress={formProgress} />

        <Form onSubmit={handleSubmit(onSubmit)}>
          {/*   */}
          <FormSection>
            <SectionTitle> </SectionTitle>
            
            <FormRow>
              <FormGroup>
                <Label> <span className="required">*</span></Label>
                <Input
                  {...register('login', {
                    required: ' ',
                    minLength: {
                      value: 3,
                      message: ' 3 '
                    },
                    pattern: {
                      value: /^[a-zA-Z0-9_-]+$/,
                      message: '  , , _  -'
                    }
                  })}
                  className={errors.login ? 'error' : ''}
                  placeholder=" "
                  autoComplete="username"
                />
                {errors.login && <ErrorText>{errors.login.message}</ErrorText>}
              </FormGroup>

              <FormGroup>
                <Label>Email <span className="required">*</span></Label>
                <EmailCodeSection>
                  <div className="email-input-wrapper">
                    <Input
                      type="email"
                      {...register('email', {
                        required: 'Email ',
                        pattern: {
                          value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                          message: '  email'
                        }
                      })}
                      className={errors.email ? 'error' : ''}
                      placeholder="your@email.com"
                      autoComplete="email"
                    />
                    {errors.email && <ErrorText>{errors.email.message}</ErrorText>}
                  </div>
                  <div className="code-button-wrapper">
                    <SecondaryButton
                      type="button"
                      onClick={handleSendCode}
                      disabled={!email || sendCodeMutation.isLoading || codeTimer > 0}
                    >
                      {sendCodeMutation.isLoading ? '...' : 
                       codeTimer > 0 ? `${codeTimer}` : ''}
                    </SecondaryButton>
                  </div>
                </EmailCodeSection>
                {codeTimer > 0 && (
                  <CodeTimer>
                       {codeTimer} 
                  </CodeTimer>
                )}
              </FormGroup>
            </FormRow>

            <FormRow>
              <CodeInputSection>
                <FormGroup>
                  <Label>  email <span className="required">*</span></Label>
                  <Input
                    type="text"
                    inputMode="numeric"
                    pattern="[0-9]*"
                    value={emailCode}
                    onChange={(e) => {
                      //      6 
                      const value = e.target.value.replace(/\D/g, '').slice(0, 6);
                      setEmailCode(value);
                    }}
                    placeholder="123456"
                    disabled={!isCodeSent}
                    maxLength="6"
                    style={{
                      textAlign: 'center',
                      letterSpacing: '8px',
                      fontWeight: 'bold',
                      fontSize: '18px',
                      fontFamily: 'monospace'
                    }}
                  />
                  {!isCodeSent && (
                    <ErrorText>    email</ErrorText>
                  )}
                  {emailCode.length > 0 && emailCode.length < 6 && isCodeSent && (
                    <ErrorText>   6 </ErrorText>
                  )}
                </FormGroup>
              </CodeInputSection>
            </FormRow>

            <FormRow>
              <FormGroup>
                <Label> <span className="required">*</span></Label>
                <InputWrapper>
                  <Input
                    type={showPassword ? 'text' : 'password'}
                    {...register('password', {
                      required: ' ',
                      minLength: {
                        value: 6,
                        message: ' 6 '
                      }
                    })}
                    className={errors.password ? 'error' : ''}
                    placeholder=""
                    autoComplete="new-password"
                  />
                  <PasswordToggle
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                  >
                    {showPassword ? <EyeOffIcon /> : <EyeIcon />}
                  </PasswordToggle>
                </InputWrapper>
                {errors.password && <ErrorText>{errors.password.message}</ErrorText>}
              </FormGroup>

              <FormGroup>
                <Label>  <span className="required">*</span></Label>
                <InputWrapper>
                  <Input
                    type={showConfirmPassword ? 'text' : 'password'}
                    {...register('confirmPassword', {
                      required: ' ',
                      validate: value =>
                        value === password || '  '
                    })}
                    className={errors.confirmPassword ? 'error' : ''}
                    placeholder=" "
                    autoComplete="new-password"
                  />
                  <PasswordToggle
                    type="button"
                    onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                  >
                    {showConfirmPassword ? <EyeOffIcon /> : <EyeIcon />}
                  </PasswordToggle>
                </InputWrapper>
                {errors.confirmPassword && <ErrorText>{errors.confirmPassword.message}</ErrorText>}
              </FormGroup>
            </FormRow>
          </FormSection>

          {/*    */}
          <FormSection>
            <SectionTitle>   </SectionTitle>
            
            <FormGroup>
              <Label>  <span className="required">*</span></Label>
              <StatusOptions>
                {statusTypes.map(status => (
                  <StatusOption
                    key={status.value}
                    className={selectedStatus === status.value ? 'selected' : ''}
                  >
                    <input
                      type="radio"
                      name="status"
                      value={status.value}
                      checked={selectedStatus === status.value}
                      onChange={(e) => setSelectedStatus(e.target.value)}
                      required
                    />
                    <span>{status.label}</span>
                  </StatusOption>
                ))}
              </StatusOptions>
            </FormGroup>

            <FormRow>
              <FormGroup>
                <Label>  <span className="required">*</span></Label>
                <Select
                  {...register('searchStatus', {
                    required: '  '
                  })}
                  className={errors.searchStatus ? 'error' : ''}
                >
                  <option value="">...</option>
                  {searchStatusTypes.map(type => (
                    <option key={type.value} value={type.value}>
                      {type.label}
                    </option>
                  ))}
                </Select>
                {errors.searchStatus && <ErrorText>{errors.searchStatus.message}</ErrorText>}
              </FormGroup>

              <FormGroup>
                <Label>  <span className="required">*</span></Label>
                <Select
                  {...register('searchAge', {
                    required: '  '
                  })}
                  className={errors.searchAge ? 'error' : ''}
                >
                  <option value="">...</option>
                  {ageRanges.map(range => (
                    <option key={range.value} value={range.value}>
                      {range.label}
                    </option>
                  ))}
                </Select>
                {errors.searchAge && <ErrorText>{errors.searchAge.message}</ErrorText>}
              </FormGroup>
            </FormRow>
          </FormSection>

          {/*   */}
          <FormSection>
            <SectionTitle> </SectionTitle>
            
            <FormRow>
              <FormGroup>
                <Label>  {selectedStatus && (selectedStatus === ' (+)' || selectedStatus === ' (+)') ? '()' : ''} <span className="required">*</span></Label>
                <Input
                  type="date"
                  {...register('manBirthday', {
                    required: '  ',
                    validate: value => {
                      const today = new Date();
                      const birthDate = new Date(value);
                      const age = today.getFullYear() - birthDate.getFullYear();
                      return age >= 18 || '    18 ';
                    }
                  })}
                  className={errors.manBirthday ? 'error' : ''}
                />
                {errors.manBirthday && <ErrorText>{errors.manBirthday.message}</ErrorText>}
              </FormGroup>

              {selectedStatus && (selectedStatus === ' (+)' || selectedStatus === ' (+)') && (
                <FormGroup>
                  <Label>  () <span className="required">*</span></Label>
                  <Input
                    type="date"
                    {...register('womanBirthday', {
                      required: '   ',
                      validate: value => {
                        const today = new Date();
                        const birthDate = new Date(value);
                        const age = today.getFullYear() - birthDate.getFullYear();
                        return age >= 18 || '    18 ';
                      }
                    })}
                    className={errors.womanBirthday ? 'error' : ''}
                  />
                  {errors.womanBirthday && <ErrorText>{errors.womanBirthday.message}</ErrorText>}
                </FormGroup>
              )}

              <LocationSelector
                countryValue={watch('country')}
                cityValue={watch('city')}
                onCountryChange={(value) => {
                  setValue('country', value);
                  clearErrors('country');
                  //     
                  if (watch('city')) {
                    setValue('city', '');
                    clearErrors('city');
                  }
                }}
                onCityChange={(value) => {
                  setValue('city', value);
                  clearErrors('city');
                }}
                countryError={errors.country?.message}
                cityError={errors.city?.message}
                required={true}
                showValidation={true}
                layout="stacked"
              />
              
              {/*    react-hook-form  */}
              <input
                type="hidden"
                {...register('country', { required: ' ' })}
              />
              <input
                type="hidden"
                {...register('city', { required: ' ' })}
              />
            </FormRow>

            <FormRow>
              <FormGroup>
                <Label> </Label>
                <Input
                  type="tel"
                  {...register('mobile')}
                  placeholder="+7 (999) 123-45-67"
                />
              </FormGroup>

              <FormGroup>
                <Label>   </Label>
                <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
                  {[
                    { id: '   (  )', label: '   (  )' },
                    { id: '   ( )', label: '   ( )' },
                    { id: ' -    ', label: ' -    ' },
                    { id: ' ', label: ' ' },
                    { id: '     ', label: '     ' }
                  ].map(option => (
                    <label key={option.id} style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                      <input
                        type="checkbox"
                        value={option.id}
                        {...register('location')}
                      />
                      <span style={{ fontSize: '14px' }}>{option.label}</span>
                    </label>
                  ))}
                </div>
              </FormGroup>
            </FormRow>

            <FormRow>
              <FormGroup>
                <Label> () {selectedStatus && (selectedStatus === ' (+)' || selectedStatus === ' (+)') ? '()' : ''}</Label>
                <Input
                  type="number"
                  {...register('manHeight')}
                  placeholder="175"
                  min="140"
                  max="220"
                />
              </FormGroup>

              {selectedStatus && (selectedStatus === ' (+)' || selectedStatus === ' (+)') && (
                <FormGroup>
                  <Label> () ()</Label>
                  <Input
                    type="number"
                    {...register('womanHeight')}
                    placeholder="165"
                    min="140"
                    max="200"
                  />
                </FormGroup>
              )}
            </FormRow>

            <FormRow>
              <FormGroup>
                <Label> () {selectedStatus && (selectedStatus === ' (+)' || selectedStatus === ' (+)') ? '()' : ''}</Label>
                <Input
                  type="number"
                  {...register('manWeight')}
                  placeholder="70"
                  min="40"
                  max="200"
                />
              </FormGroup>

              {selectedStatus && (selectedStatus === ' (+)' || selectedStatus === ' (+)') && (
                <FormGroup>
                  <Label> () ()</Label>
                  <Input
                    type="number"
                    {...register('womanWeight')}
                    placeholder="55"
                    min="40"
                    max="150"
                  />
                </FormGroup>
              )}
            </FormRow>

            <FormRow>
              <FormGroup>
                <Label>   {selectedStatus && (selectedStatus === ' (+)' || selectedStatus === ' (+)') ? '()' : ''}</Label>
                <Select {...register('manSmoking')}>
                  {smokingOptions.map(option => (
                    <option key={option.value} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </Select>
              </FormGroup>

              {selectedStatus && (selectedStatus === ' (+)' || selectedStatus === ' (+)') && (
                <FormGroup>
                  <Label>   ()</Label>
                  <Select {...register('womanSmoking')}>
                    {smokingOptions.map(option => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </Select>
                </FormGroup>
              )}
            </FormRow>

            <FormRow>
              <FormGroup>
                <Label>   {selectedStatus && (selectedStatus === ' (+)' || selectedStatus === ' (+)') ? '()' : ''}</Label>
                <Select {...register('manAlko')}>
                  {alkoOptions.map(option => (
                    <option key={option.value} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </Select>
              </FormGroup>

              {selectedStatus && (selectedStatus === ' (+)' || selectedStatus === ' (+)') && (
                <FormGroup>
                  <Label>   ()</Label>
                  <Select {...register('womanAlko')}>
                    {alkoOptions.map(option => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </Select>
                </FormGroup>
              )}
            </FormRow>

            <FormGroup>
              <Label> </Label>
              <TextArea
                {...register('info')}
                placeholder="  ,    ..."
              />
            </FormGroup>
          </FormSection>

          <Button
            type="submit"
            disabled={registerMutation.isLoading || !emailCode}
          >
            {registerMutation.isLoading ? '...' : ''}
          </Button>
        </Form>

        <LoginLink>
            ? <Link to="/login"></Link>
        </LoginLink>
      </RegisterCard>
    </RegisterContainer>
  );
};

export default Register;